import React from 'react'
import Link from 'gatsby-link'

import RandomAnimation from '../assets/RandomAnimation.js'
import IllAvatar from '../assets/IllAvatar.js'
import IconContactWithColor from '../assets/icons/IconContactWithColor.js'
import IconWebSkills from '../assets/icons/IconWebSkills.js'
import IconResume from '../assets/icons/IconResume.js'
import IconSocial from '../assets/icons/IconSocial.js'
import Updates from '../_includes/Updates.js'

const IndexPage = ({ data }) => (
  <main className="home-page">
    <section className="home__section-one">
      <header className="home__section-one__header row">
        <div className="home__section-one__header__col-left">
          <h1 className="h-1">Benjamin Modayil</h1>
          <p>is a Front-End Developer from Philadelphia, Pennsylvannia.</p>
          <a href="#bio" className="home__section-one__header__link link__read">
            learn more
          </a>
        </div>
        <div className="home__section-one__header__col-right">
          <RandomAnimation />
        </div>
      </header>
      <ul className="article-loop">
        {data.allMarkdownRemark.edges.map(post => (
          <li>
            <Link key={post.node.id} to={post.node.frontmatter.path}>
              <h3 className="h-3">{post.node.frontmatter.title}</h3>
              <p>
                <span>tl;dr:</span>
                {post.node.frontmatter.tldr}
              </p>
              <small className="article-loop__date">date</small>
            </Link>
          </li>
        ))}
      </ul>
    </section>

    <section className="home__section-two">
      <div className="home__section-two__row">
        <div className="home__section-two__row__col-left">
          <p className="text-level text-level-big">
            Okay. Enough third-person talk. Im a
            <a href="https://google.com">Front-End Developer</a> currently living in{' '}
            <a>Falls Church, VA </a>. I work with <a>Viget</a> in the same position, but
            as an apprentice learning about the industry.
          </p>
          <p className="text-level text-level-big">
            If youre looking for work examples you can check out one of the sites linked
            here or my experiments below:
          </p>
        </div>
        <div className="home__section-two__row__col-right">
          <IllAvatar />
        </div>
      </div>
      <ul className="article-loop">
        {data.allMarkdownRemark.edges.map(post => (
          <li>
            <Link key={post.node.id} to={post.node.frontmatter.path}>
              <h3 className="h-3">{post.node.frontmatter.title}</h3>
              <p>{post.node.frontmatter.tldr}</p>
            </Link>
          </li>
        ))}
      </ul>
    </section>
    <section className="home__section-three">
      <div className="home__section-three__row">
        <div className="home__section-three__row__col-left">
          <p className="text-level text-level-medium">
            I really love learning technologies. Im not lying here. Heres a few things
            that I have been able to add to my skillset.
          </p>
        </div>

        <div className="home__section-three__row__col-right">
          <IconWebSkills />
        </div>
      </div>

      <div className="home__section-three__row__col-left">
        <p className="text-level text-level-medium">
          If you need a <a href="/static/files/resumeModayil.pdf"> resume </a> then just
          click on that keyword. Also feel free to check-out my{' '}
          <a href="https://codepen.io/modayilme">Codepen</a> and below for more examples
          of this things I like to learn.
        </p>
      </div>

      <div className="home__section-three__row__col-right">
        <IconResume />
      </div>

      <div className="home__section-three__row__col-left">
        <p className="text-level text-level-medium">
          If youre wondering what Im like outside of work, well, sorry to be a bummer,
          but I like to work. Usually Im learning something new code-wise, reading a{' '}
          <a href="https://www.goodreads.com/user/show/50421880-benjamin">book</a>/comics,
          listening to a podcast, or perhaps, on the rare occasion, relaxing.
        </p>
        <p className="text-level text-level-medium">
          Interested in following me or keeping updated on things I write or do? Sign up
          for my newsletter below, follow me on my{' '}
          <a href="https://twitter.com/modayilme">Twitter</a>,{' '}
          <a href="https://www.instagram.com/modayilme/">Instagram</a>, or connect with me
          on{' '}
          <a href="https://www.linkedin.com/in/benjamin-modayil-89136411a/">LinkedIn</a>.
        </p>

        <div className="home__section-three__row__col-right">
          <IconSocial />
        </div>
      </div>
    </section>
    <div className="banner-newsletter">
      <Updates />
    </div>
  </main>
)

export const pageQuery = graphql`
  query IndexQuery {
    allMarkdownRemark(limit: 10) {
      edges {
        node {
          id
          frontmatter {
            title
            tldr
            path
          }
        }
      }
    }
  }
`
export default IndexPage



